export default {
  components: {
    // FormData
  },
  data () {
    return {
      default_value: {
        create_time: '2017-12-05',
        create_time_range_init: '2017-6-12  2017-9-16', // ['2017-6-12','2017-9-16']
        create_year: '2016',
        create_month: '11',
        create_week: '2017-3-8'
      },
      fields: [{
        label: '',
        type: 'date',
        key: 'create_time',
        placeholder: '',
        events: {
          change: (value) => {
            this.$message('' + value)
          }
        }
      }, {
        label: '',
        type: 'daterange',
        key: 'create_time_range_init',
        placeholder: '-',
        date_range_attrs: {
          'range-separator': '  '
        },
        events: {
          change: ({value, info}) => {
            this.$message('' + value + '' + info)
          }
        }
      }, {
        label: '',
        type: 'year',
        key: 'create_year',
        placeholder: '',
        events: {
          change: (value) => {
            this.$message('' + value)
          }
        }
      }, {
        label: '',
        type: 'month',
        key: 'create_month',
        placeholder: '',
        events: {
          change: (value) => {
            this.$message('' + value)
          }
        }
      }, {
        label: '',
        type: 'week',
        key: 'create_week',
        placeholder: '',
        date_week_attrs: {
          format: 'yyyy - WW'
        },
        events: {
          change: (value) => {
            this.$message('' + value)
          }
        }
      }]

    }
  },
  methods: {
    onSubmit (data) {
      this.$emit('onTest', data)
    }
  }
}



