import {
  store
} from 'utils/'

export default {
  // 
  userinfo: store.get('userinfo') || {},

  // 
  // 
  remumber: {
    remumber_flag: !!store.get('remumber_flag'),
    remumber_login_info: store.get('remumber_login_info') || {
      username: '',
      token: ''
    }
  }
}



