export default {
  components: {},
  data () {
    return {
      default_value: {
        sex: 0
      },
      fields: [{
        label: '',
        key: 'sex',
        type: 'radio',
        desc: 'radio',
        list: [{
          text: '',
          value: 0
        }, {
          text: '',
          value: 2
        }, {
          text: '',
          value: 3
        }],
        events: {
          change: ({value, info}) => {
            this.$message('' + value + '' + info)
          }
        }
      }]
    }
  },
  methods: {
    onSubmit (data) {
      this.$emit('onTest', data)
    }
  }
}



