export default {
  name: 'article-list',
  components: {},
  data () {
    return {
      list: [],
      fields: [{
        key: 'create_time',
        label: ''
      }, {
        key: 'title',
        label: ''
      }],
      search_data: {
        setting: {
          inline: true
        },
        fields: [{
          key: 'cate',
          type: 'select',
          multiple: false,
          list: [{
            value: 'jishu',
            text: ''
          }, {
            value: 'sanwen',
            text: ''
          }, {
            value: 'qita',
            text: ''
          }],
          desc: '',
          label: ''
        }, {
          label: '',
          key: 'input',
          desc: ''
        }]
      }
    }
  },
  methods: {
    onSearch (data) {
      this.$message('' + JSON.stringify(data))
    },
    init () {

    }
  },
  mounted () {
    this.init()
  }
}



