export default {
  name: 'btn-condition',
  components: {},
  data () {
    return {
      list: [{
        status: 1
      }, {
        status: 2
      }, {
        status: 3
      }],
      fields: [{
        key: 'status',
        label: '',
        formatter (data) {
          var status = ''
          switch (data.status) {
            case 1:
              status = ''
              break
            case 2:
              status = ''
              break
            case 3:
              status = ''
              break
          }
          return status
        }
      }],

      btn_info: {
        condition: {
          batch: false,
          add: false,
          select: false,
          update: false,
          delete ({data}) {
            return data.status === 2
          },
          list: [{
            text: '',
            condition ({data}) {
              return data.status === 2
            },
            fn ({data}) {
              data.status = 1
            }
          }, {
            text: '',
            type: 'warning',
            condition ({data}) {
              return data.status === 1 || data.status === 3
            },
            fn ({data}) {
              data.status = 2
            }
          }]
        }
      }
    }
  },
  created () {
  },
  methods: {
    onClickBtnDelete ({dataIndex, data}) {
      this.$message('' + dataIndex)

      // 
      // this.list[dataIndex].status = 3//

      // 
      data.status = 3
    },
    init () {

    }
  },
  mounted () {
    this.init()
  },
  watch: {
    $route () {

    }
  }
}



