import fa from "element-ui/src/locale/lang/fa";

var gbs = {
//  host: '/srm', // slsadmin.api.sls.com,Nginx
  db_prefix: 'devops_', // key
  // 
  api_status_key_field: 'code',
  // value
  api_status_value_field: 200,

  // 
  api_data_field: 'data',

  api_custom: {
    401: function (res) {
      this.$store.dispatch('remove_userinfo').then(() => {
        //this.$router.push('/login')
        //window.location.href = res['data'].redirect_url;
        console.info("redirect_url==" + res['data'].redirect_url);
        /*$.ajax({
          url: res['data'].redirect_url,
          type: "get",
          xhrFields: {
            withCredentials: true // cookie
          },
          success: function(resp){
            console.info("success");
          },
          error: function(req, status, errmsg){
            console.info(errmsg);
          }
        });*/

        $.ajax({
          url: res['data'].redirect_url,   //url
          dataType: "jsonp",
          type: "get",
          xhrFields: {
            withCredentials: true // cookie
          },
          success: function (data) {
            console.info("success");
          }
        })

        //window.location.href = res['data'].redirect_url

      })
    }
  }
}

var cbs = {
  /**
   * ajax200
   * @param  {object} err 
   */
  statusError (err) {
    console.log('err')
    if (err.status !== 404) {
      this.$message({
        showClose: true,
        message: '' + err.msg,
        type: 'error'
      })
    } else {
      this.$store.dispatch('remove_userinfo').then(() => {
        this.$alert(err.status + ',' + err.msg + '', '', {
          confirmButtonText: '',
          callback: action => {
            //this.$router.push('/login')
          }
        })
      })
    }
  },

  /**
   * ajax
   */
  requestError (err) {
    this.$message({
      showClose: true,
      message: '' + (err.response ? err.response.status : '') + ',' + (err.response ? err.response.statusText : ''),
      type: 'error'
    })
  }
}

export {
  gbs,
  cbs
}



