
export default {
  name: 'managemant',
  data() {
    return {
      formInline: {
        user: '',
        region: ''
      },
      //
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: true,
      // 
      tableData: [],
      //,
      dialogVisible: false,
      // formatter:[],
      //
      ruleForm: {
        id: '',
        group: [],
        desc: '',
        environment: [],
        tableData1: [{
          enable: true,
          hostId: '',
          host: '',
          ip: '',
          port: '',
          remark: '',
        }]
      },
      grouplist:[],
      options: [{
          value: 1,
          label: 'dev'
        }, {
          value: 2,
          label: 'test'
        }],
      dialogLoading: false,
      // 
      rules: {
        group: [
          {type:'number', required: true, message: '', trigger: 'change' },
        ],
        environment: [
          {type:'number', required: true, message: '', trigger: 'change' }
        ],
        remark: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ],
        ip: [
          { required: true, message: 'ip', trigger: 'blur' },
          { pattern: /^(1\d{2}|2[0-4]\d|25[0-5]|[1-9]\d|[1-9])(\.(1\d{2}|2[0-4]\d|25[0-5]|[1-9]\d|\d)){3}$/, message: 'ip' }
        ],
        port: [
          { type:'number',required: true, message: '', trigger: 'blur'},
          { pattern: /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, message: '0-65535' }
          //{ required: true, message: 'ip', trigger: 'blur' }
        ],
        host: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
          //{ required: true, message: 'ip', trigger: 'blur' }
        ],
        
      },
    
      selectList: [],
      dialogTitle: '',
      selectProp: {
        group: '',
        environment: '',
      },
      isSaved: true,
      instanceIndex: '',
      // 
      groupData: [],
      envirData: [],
      envirFormData: [],
      instanceData: [],
      selectdisabled: false,

      //allHost
      allHost: [],

    }
  },
  mounted() {
    this.getGroup();
    this.getData();
    this.getAllHost();
  },
  // 
  methods: {
      getenvir(flag){
        var clusterId;
        if(flag==0){
          this.envirData=[];
          this.selectProp.environment="";
          clusterId=this.selectProp.group;
          if(clusterId==""){
            return;
          }
        }else{
          this.envirFormData=[];
          this.ruleForm.environment="";
          clusterId=this.ruleForm.group
          if(clusterId==""){
            return;
          }
        }
        this.$$api_instanceman_envirlist({
          data: {
            clusterId: clusterId
          },
          fn: data => {
            if(data.code == 200){
              if(flag==0){
                this.envirData = data.data.envlist;
              }else{
                this.envirFormData = data.data.envlist;
              }
            }else{
              this.$alert(data.message, '', {
                confirmButtonText: ''
              });
            }
          },
          errFn: () => {
            this.$alert('', '', {
              confirmButtonText: '',
            });
          }
        })
      },
      // 
      getGroup() {
        this.$$api_instanceman_grouplist({
          fn: data => {
            if(data.code == 200){
              this.groupData = data.data.grouplist;
            }else{
              this.$alert(data.message, '', {
                confirmButtonText: ''
              });
            }
          },
          errFn: () => {
            this.$alert('', '', {
              confirmButtonText: '',
            });
          }
        })
      },
    deleteRow(id) {
      this.loading = true;
      this.$$api_instanceman_deleteenv({
        data: {
          id: id
        },
        fn: data => {
          this.getData();
        },
        errFn: () => {
          
        }
      })
    },
    // 
    addRow() {
      var saved = true;
      for(let rowData of this.ruleForm.tableData1){
        if(typeof rowData.id === 'undefined') saved = false
      }
      // if(saved){
        this.ruleForm.tableData1.push({
          enable: true,
          host: '',
          ip: '',
          port: '',
          remark: '',
        })
      // }else{
      //   if(this.ruleForm.tableData1.length === 1){
      //     this.$alert('', '', {
      //       confirmButtonText: '',
      //     });
      //   }else{
      //     this.$alert('', '', {
      //       confirmButtonText: '',
      //     });
      //   }
      // }
    },
    // &&
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    getData() {
      this.$$api_instanceman_list({
        data: {
          name : this.selectProp.group,
          evnsci : this.selectProp.environment,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
        },
        fn: data => {
          this.loading = false;
          if(data.code == 200){
            this.total = data.data.page.total;
            this.tableData = data.data.list;
          }else{
            this.$alert(data.message, '', {
              confirmButtonText: ''
            });
          }
        },
        errFn: () => {
          this.loading = false;
          this.$alert('', '', {
            confirmButtonText: '',
          });
        }
      })
    },
    currentChange(i) {
      this.loading = true;
      this.pageNum = i;
      this.getData();
    },
    addOne() {
    this.ruleForm.tableData1 = [{ enable: true,
      host: '',
      ip: '',
      port: '',
      remark: '',}];
      this.ruleForm.group = "";
      this.ruleForm.environment = "";
      this.envirFormData = [];
      this.dialogVisible = true;
      this.dialogTitle = '';
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.saveOfUpdateForm();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      //}
    },
    saveOfUpdateForm() {
      this.selectdisabled = false;
      this.dialogLoading = true;
        for(var i = 0; i < this.ruleForm.tableData1.length; i++){
          this.ruleForm.tableData1[i].enable === true? this.ruleForm.tableData1[i].enable = 1 : this.ruleForm.tableData1[i].enable = 0;
        }
      this.$$api_instanceman_insert({
        data: {
          appInstance: this.ruleForm.tableData1,
          deptId: 1,
          createBy: 1,
          updateBy: 1,
          envId: this.ruleForm.environment,
          remark: this.ruleForm.desc,
          id: this.ruleForm.group,
        },
        fn: data => {
          this.dialogLoading = false;
          if(data.code == 200){
            this.dialogVisible = false;
            this.getData();
          }else{
            this.$alert(data.message, '', {
              confirmButtonText: ''
            });
          }
          
        },
        errFn: () => {
          this.dialogLoading = false;
          this.$alert('', '', {
            confirmButtonText: '',
          });
        }
      })
    },
    selectGo(data) {
      this.selectList = data;
    },
    //
    inquiry(id,envId) {
      this.dialogLoading = true;
      this.$$api_instanceman_select({
        data: {
          id: id,
          evnsci: envId
        },
        fn: data => {
          if(data.code == 200){
            this.ruleForm.id = data.data.iof.id;
            this.ruleForm.group = data.data.iof.id;
            this.ruleForm.desc = data.data.iof.remark;
            this.ruleForm.environment = data.data.iof.appInstance[0].envId;
            if(null != data.data.iof.appInstance){
              for(var i = 0; i < data.data.iof.appInstance.length; i++){
                data.data.iof.appInstance[i].enable === 1? data.data.iof.appInstance[i].enable = true : data.data.iof.appInstance[i].enable = false;
              }
              this.ruleForm.tableData1 = data.data.iof.appInstance;
              console.info(this.ruleForm.tableData1);
            }
            
          }else{
            this.$alert(data.message, '', {
              confirmButtonText: ''
            });
          }
          this.dialogLoading = false;
        },
        errFn: () => {
          this.dialogLoading = false;
          this.$alert('', '', {
            confirmButtonText: '',
          });
        }
      })
    },
    editOne(row) {
      this.dialogTitle = 'Instance information edit';
      this.dialogVisible = true;
      this.selectdisabled = true;
      this.inquiry(row.id,row.evnsci);

    },
    closeDialog(){
      this.ruleForm.tableData1 = [{
        enable: false,
        host: '',
        ip: '',
        port: '',
        remark: '',
      }],
      this.ruleForm = {
        id: '',
        group: '',
        desc: '',
        environment: '',
      }
    },
    queryList(){
      this.loading = true;
      this.getData();
    },
    handleEdit(index,row){
      if('' === this.instanceIndex) this.instanceIndex = index;
      // if(index != this.instanceIndex && false === this.isSaved){
      //   this.$alert('', '', {
      //     confirmButtonText: '',
      //   });
      // }else{
      //   this.isSaved = false;
        this.instanceIndex = index;
      //}
    },
    deleteInstance(index, id) {
      if(id==undefined){
        this.ruleForm.tableData1.splice(index, 1);
      }else{
        this.$$api_instanceman_deleteInstance({
          data: {
            id: id
          },
          fn: data => {
            this.ruleForm.tableData1.splice(index, 1);
          },
          errFn: () => {
            
          }
        })
      }
     // }
    },
    submitInstance(rowData){
      this.dialogLoading = true;
      if(typeof rowData.id != 'undefined' && '' != rowData.id){
        this.$$api_instanceman_updateInstance({
          data: {
            id: rowData.id,
            enable: rowData.enable===true?1:0,
            host: rowData.host,
            ip: rowData.ip,
            port: rowData.port,
            remark: rowData.remark
          },
          fn: data => {
            this.dialogLoading = false;
            if(data.code == 200){
              this.isSaved = true;
              this.$message({
                message: '',
                type: 'success'
              });
            }else{
              this.$alert(data.message, '', {
                confirmButtonText: ''
              });
            }
          },
          errFn: () => {
            this.dialogLoading = false;
            this.$alert('', '', {
              confirmButtonText: '',
            });
          }
        })
      }else{
        var data = [{
            enable: rowData.enable===true?1:0,
            host: rowData.host,
            ip: rowData.ip,
            port: rowData.port,
            remark: rowData.remark
        }];
        this.$$api_instanceman_insertInstance({
          data: {
            id: this.ruleForm.id,
            appInstance: data
          },
          fn: data => {
            this.dialogLoading = false;
            if(data.code == 200){
              this.isSaved = true;
              this.dialogVisible = false;
              this.getData();
            }else{
              this.$alert(data.message, '', {
                confirmButtonText: ''
              });
            }
          },
          errFn: () => {
            this.dialogLoading = false;
            this.$alert('', '', {
              confirmButtonText: '',
            });
          }
        })
      }
    },


    //host
    // 
    getAllHost() {
      this.$$api_share_allHost({
        fn: data => {
          if(data.code == 200){
            this.allHost = data.data.list;
          }else{
            this.$alert(data.message, '', {
              confirmButtonText: ''
            });
          }
        },
        errFn: () => {
          this.$alert('', '', {
            confirmButtonText: '',
          });
        }
      })
    },


  },

}



