import echarts from 'echarts'

export default {
  name: 'echarts',
  data () {
    return {
      chartDom: null,
      data: {
        title: '',
        subtext: ''
      }
    }
  },
  methods: {
    init () {
      // domecharts
      this.chartDom = echarts.init(document.getElementById('chartDom'))
      return this
    },
    update () {
      if (this.chartDom === null) {
        this.init()
      }
      this.chartDom.setOption({
        title: {
          text: 'line Chart'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['', '', '']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['', '', '', '', '', '', '']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '',
          type: 'line',
          stack: '',
          data: [120, 132, 101, 134, 90, 230, 210]
        }, {
          name: '',
          type: 'line',
          stack: '',
          data: [220, 182, 191, 234, 290, 330, 310]
        }, {
          name: '',
          type: 'line',
          stack: '',
          data: [820, 932, 901, 934, 1290, 1330, 1320]
        }]
      })
    }
  },
  mounted: function () {
    this.init()
      .update()
  }
}



