export default {
  name: 'article-list',
  components: {
    // ListData
  },
  data () {
    return {
      types: {
        'A': '',
        'B': '',
        'C': '',
        'D': ''
      },
      list: [{
        id: 1,
        name: '',
        type: 'A',
        status: 1
      }, {
        id: 2,
        name: '',
        type: 'B',
        status: 2
      }, {
        id: 3,
        name: '',
        type: 'C',
        status: 1
      }],
      fields: [{
        key: 'name',
        label: ''
      }, {
        key: 'type',
        label: '',
        formatter: (item) => {
          return this.types[item.type]
        }
      }, {
        key: 'status',
        label: '',
        formatter: (item) => {
          return item.status === 1 ? '' : ''
        }
      }]
    }
  },
  methods: {
    // {type,data,dataIndex,list,btn,btnIndex}
    onClickBtn (opts) {
      console.log(opts)
    },

    onClickBtnAdd () {
      this.$message('')
    },
    onClickBtnUpdate () {
      this.$message('')
    },
    onClickBtnSelect () {
      this.$message('')
    },
    onClickBtnDelete () {
      this.$message('')
    }
  },
  mounted () {

  },
  watch: {
    $route (to, from) {

    }
  }

}



