export default {
  data () {
    return {
      default_value: {},
      fields: [
        {
          label: 'Input',
          key: 'input',
          desc: '...'
        },
        {
          key: 'cate',
          type: 'select',
          desc: '',
          label: '',
          list: [{
            value: 'jishu',
            text: ''
          }, {
            value: 'sanwen',
            text: ''
          }, {
            value: 'qita',
            text: ''
          }]
        }, {
          key: 'tabs',
          type: 'select',
          desc: '',
          label: '',
          multiple: true,
          list: [{
            value: 'html',
            text: 'HTML'
          }, {
            value: 'css',
            text: 'CSS'
          }, {
            value: 'javascript',
            text: 'JavaScript'
          }, {
            value: 'jquery',
            text: 'query'
          }]
        },
        {
          label: '',
          key: 'sex',
          type: 'radio',
          desc: 'radio',
          list: [{
            text: '',
            value: 0
          }, {
            text: '',
            value: 2
          }, {
            text: '',
            value: 3
          }]
        },
        {
          key: 'status',
          type: 'switch',
          value: {
            on: '',
            off: ''
          },
          desc: '',
          label: ''
        },
        {
          label: '',
          type: 'checkbox',
          key: 'love',
          list: [{
            text: '',
            value: 'chi'
          }, {
            text: '',
            value: 'he'
          }, {
            text: '',
            value: 'wan'
          }]
        }
      ],
      rules: {
        input: [{
          required: true, message: '', trigger: 'blur'
        }],
        cate: [{
          required: true, message: '', trigger: 'blur'
        }],
        sex: [{
          required: true, message: '', trigger: 'blur'
        }],
        tabs: [{
          type: 'array', required: true, message: '', trigger: 'blur'
        }],
        love: [{
          type: 'array', required: true, message: '', trigger: 'blur'
        }]
      }

    }
  },
  methods: {
    onSubmit (data) {
      this.$emit('onTest', data)
    }
  }
}



