import {
  store
} from 'utils/'
/**
 * Created by sailengsi on 2017/5/11.
 */
export default {
  name: 'login',
  data () {
    return {
      winSize: {
        width: '',
        height: ''
      },
      formOffset: {
        position: 'absolute',
        left: '',
        top: ''
      },
      register: false,
      login_actions: {
        disabled: false
      },
      data: {
        username: '',
        password: ''
        // token: ''
      },
    }
  },
  methods: {

  },
  mounted () {
      //var iamBaseURI = "http://passport.anjiancloud.test/sso";
      //var iamBaseURI = "http://passport.wl4g.com/sso";
      var iamBaseURI = "http://localhost:14040/iam-server";
  
      // IAM SDKSNS
      (function () {
        window.IAM.Core.configure({
          baseUri: iamBaseURI, // IAMURI
          // 
          captcha: {
            use: "VerifyWithJigsawGraph", // default by 'VerifyWithGifGraph'
            panel: document.getElementById("jigsaw"), // Jigsaw
            img: document.getElementById("codeimg"), //  imgjpeg/gif
            input: document.getElementById("captcha_input"), // inputjpeg/gif
            onSuccess: function(verifiedToken) {
              console.debug("Captcha verify successful. verifiedToken is "+ verifiedToken);
            },
            onError: function(errmsg) { // :
              console.warn(errmsg);
            }
          },
          // 
          account: {
            submitBtn: document.getElementById("iam_submit"), // 
            principal: document.getElementById("user_name"), // 
            credential: document.getElementById("password"), // 
            onBeforeSubmit: function (principal, plainPasswd, captcha) { // 
              //console.log("....");
              return true;
            },
            onSuccess: function (principal, redirectUrl) {
              //console.log("!");
              return true; // false
            },
            onError: function (errmsg) {
              console.error(". " + errmsg);
              $("#err_tip").text(errmsg).show().delay(5000).hide(100);
            }
          },
          sms: { // SMS
            submitBtn: document.getElementById("phone_submit"), // 
            sendSmsBtn: document.getElementById("code-get"), // SMS
            mobileArea: $(".select-area"), // select
            mobile: document.getElementById("user_phone"), // input
            smsCode: document.getElementById("codeNumber"), // SMSinput
            onBeforeSubmit: function (mobileNum, smsCode) {
              //console.log("....");
              return true;
            },
            onSuccess: function(resp){
              $('.err-tip').text('');
              $('.code-write').hide();
            },
            onError: function(errmsg){
              console.error(errmsg);
              $("#err_tip").text(errmsg).show().delay(8000).hide(100);
            }
          },
          // SNS
          sns: {
            // 
            required: {
              which: function () { // "login","bind"
                return "login";
              },
              //refreshUrl: "" // SNSURLwhich=login
            },
            // 
            qrcodePanel: {
              src: document.getElementById("qrcode_show"),
              width: "250",
              height: "260"
            },
            // TAB
            pagePanel: {
              "width": "800px",
              "height": "500px",
              "left": "250px",
              "top": "100px"
            },
            // 
            provider: {
              // "qq": { // (, qq/wechat/sina/github/google/dingtalk/twitter/facebook)
              // 	panelType: "pagePanel", // TAB
              // 	src: document.getElementById("qq") // QQ
              // },
              "wechat": { // (, qq/wechat/sina/github/google/dingtalk/twitter/facebook)
                panelType: "qrcodePanel", // 
                src: document.getElementById("codeGo") // Wechat
              }
            },
            // SNS
            onBefore: function (provider, panelType) {
              if (provider == 'wechat') { // tab
                changeTab('login_scan_panel', 'login_scan_pass');
              }
              // false
              return true;
            }
          }
        }).bindForAccountAuthenticator().bindForSMSAuthenticator().bindForSNSAuthenticator().bindForCaptchaVerifier();
      })();
    
      // panelTypepagePanelSNS
      (function () {
        window.onmessage = function (e) {
          window.location.href = JSON.parse(e.data).refresh_url;
        }
      })();

      // SNS:WeChatIAM
      (function() {
        var url = iamBaseURI + "/login/errread";	
        $.ajax({
          url: url,
          success: function (res) {
            //console.log(res);
            var errmsg = res.data["errorTipsInfo"];
            if (errmsg != null && errmsg.length > 0) {
              $("#err_tip").text(errmsg).show().delay(8000).hide(100);
            }
          }
        });
      })();

  },

}



