export default {
  components: {
    // FormData
  },
  data () {
    return {
      default_value: {
        status: true
      },
      fields: [{
        key: 'status',
        type: 'switch',
        value: {
          on: '',
          off: ''
        },
        desc: '',
        label: '',
        events: {
          change: ({info, value}) => {
            this.$message('' + value + '' + info)
          }
        }
      }]
    }
  },
  methods: {
    onSubmit (data) {
      this.$emit('onTest', data)
    }
  }
}



