<template>
  <div>
    <div
      style="width: 1100px;"
      :id="id"></div>
  </div>
</template>

<script>
  import WangEditor from 'wangeditor'
  import Common from './js/Common'

  var Js = Common('sls-textarea')
  Js.mixins = [{
    data () {
      return {
        is_init: true,

        editor: null,

        id: '',

        menus: [
          'head',  // 
          'bold',  // 
          'italic',  // 
          'underline',  // 
          'strikeThrough',  // 
          'foreColor',  // 
          'backColor',  // 
          'link',  // 
          'list',  // 
          'justify',  // 
          'quote',  // 
          'emoticon',  // 
          'image',  // 
          'table',  // 
          'video',  // 
          'code',  // 
          'undo',  // 
          'redo'  // 
        ],

        attrs: {
          zIndex: 90,

          uploadImgShowBase64: false,   //  base64 
          uploadImgServer: '', // 
          showLinkImg: true, // tab
          uploadImgMaxSize: 5 * 1024 * 1024, //  5M
          uploadImgMaxLength: 10000, // , 10000 
          uploadImgParams: {}, // token url formdata
          uploadFileName: '', // filenameformdata.append(name, file)
          uploadImgHeaders: {}, //  header
          withCredentials: false, //  cookie  withCredentials
          uploadImgTimeout: 5000 //  timeout  5 
        },

        event: {
          // 
          // xhr  XMLHttpRequst editor files 
          // before: function (xhr, editor, files) {},

          // 
          // xhr  XMLHttpRequst editor result 
          // success: function (xhr, editor, result) {},

          // 
          // xhr  XMLHttpRequst editor result 
          // fail: function (xhr, editor, result) {},

          // 
          // xhr  XMLHttpRequst editor 
          // error: function (xhr, editor) {},

          // 
          // xhr  XMLHttpRequst editor 
          // timeout: function (xhr, editor) {},

          //  {errno:0, data: [...]} 
          //  JSON 
          // 
          // insertImg editor result 
          //  {url:'....'} 
          // result  JSON 
          /* customInsert: function (insertImg, result, editor) {
           var url = result.url
           insertImg(url)
          } */
        }
      }
    },
    computed: {
      config () {
        return this.Data.config || {}
      }
    },
    methods: {
      /**
       * 
       */
      initParams () {
        this.id = this.data.id || this.data.key

        if (this.config.show_menus && Array.isArray(this.config.show_menus)) {
          this.menus = this.menus.filter(item => {
            return this.config.show_menus.indexOf(item) !== -1
          })
        } else {
          if (this.config.hide_menus && Array.isArray(this.config.hide_menus)) {
            this.menus = this.menus.filter(item => {
              return this.config.hide_menus.indexOf(item) === -1
            })
          }
        }
      },

      onSetEditorContent () {

      },

      /**
       * 
       */
      initWangEditor () {
        this.editor = new WangEditor('#' + this.id)
        this.editor.customConfig = {}
        this.editor.customConfig.menus = this.menus
        this.editor.customConfig = Object.assign(this.editor.customConfig, this.attrs, this.config.attrs)
        this.editor.customConfig.uploadImgHooks = {}
        this.editor.customConfig.uploadImgHooks = Object.assign(this.editor.customConfig.uploadImgHooks, this.config.events)
        this.editor.customConfig.onchange = (html) => {
          this.submit_data[this.data.key] = html
          this.submit_info[this.data.key] = this.editor.txt.text()
          this.config.onchange && this.config.onchange({
            value: this.submit_data[this.data.key],
            info: this.submit_info[this.data.key]
          })
        }
        this.editor.create()

        console.log(this.editor.txt.html)
        console.log(this.editor.txt.text)
      }
    },
    created () {
      this.initParams()
    },
    mounted () {
      this.initWangEditor()
    },
    watch: {
      submit_data: {
        deep: true,
        handler (v) {
          /* this.submit_data = v;
                    if (this.editor && this.is_init === true) {
                        this.is_init = false;
                        this.editor.txt.html(this.submit_data[this.data.key]);
         } */
        }
      }
    }
  }]
  export default Js
</script>

<style scoped lang="less">

</style>



